@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,200&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: 'Poppins', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  box-sizing: border-box;
}
button,button:hover{
  font-family: 'Poppins', sans-serif;
}
.search_form {
    display: -webkit-flex;
    display: flex;
    text-align: center;
    min-width: 100vw;
    background-color: rgb(194, 108, 158);
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
}

.search_bar,
.search_button {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    box-sizing: border-box;
    padding: 1rem;
    height: 2.5rem;
    margin: 1rem;
    outline: none;
    border: rgba(216, 123, 79, 0.534) solid 2px;
    border-radius: 0.5rem;
    font-size: 1rem;
    font-weight: 600;
    transition: ease-in-out;
    transition-duration: 300ms;

}

.search_bar{
    width: auto;
    -webkit-flex-grow: 1;
            flex-grow: 1;
}

.search_button:hover {
    background-color: rgba(216, 122, 79, 0.911);
}

.search_bar:hover {
    border: rgb(100, 84, 84) solid 2px;
    transition: ease-in-out;
    transition-duration: 300ms;
}

/* -------------recepi styles----------------- */
/* -------------recepi styles----------------- */

.recipe {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    -webkit-align-items: flex-start;
            align-items: flex-start;
}

.recipe_box {
    width: 22.5rem;
    margin: 2rem;
    padding: 1rem;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: flex-start;
            align-items: flex-start;
    background-color: rgb(202, 202, 202);
    border-radius: 0.75rem;
    box-shadow: 0px 7px 14px 1px rgba(0, 0, 0, 0.54);
    -webkit-box-shadow: 0px 7px 14px 1px rgba(0, 0, 0, 0.54);
    -moz-box-shadow: 0px 7px 14px 1px rgba(0, 0, 0, 0.54);
    transition: ease-in-out;
    transition-duration: 300ms;
    overflow: hidden;
    -webkit-flex-flow: column;
            flex-flow: column;
}

.recipe_image {
    margin-bottom: 0.5rem;
    min-height: 12rem;
    min-width: 22.5rem;
    background-color: lavender;
    border-radius: 0.75rem;
}

.recipe_box:hover {
    -webkit-transform: scale(1.05, 1.05);
            transform: scale(1.05, 1.05);
    box-shadow: 0px 7px 28px 3px rgba(0, 0, 0, 0.52);
    -webkit-box-shadow: 0px 7px 28px 3px rgba(0, 0, 0, 0.52);
    -moz-box-shadow: 0px 7px 28px 3px rgba(0, 0, 0, 0.52);
}

.recipe_view{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    text-align: center;
}
.recipe_view_button{
    text-decoration: none;
    width: 21.5rem;
    padding: 0.5rem;
    color: rgb(61, 60, 60);
    background-color: rgb(177, 177, 177);
    font-weight: 700;
    border-radius: 0.75rem;
    transition: ease-in-out;
    transition-duration: 300ms;
}
.recipe_view_button:hover{
    background-color: rgb(63, 63, 63);
    color: rgb(192, 192, 192);
}
@media only screen and (max-width: 600px){
    .recipe_box{
        margin: 2rem 0px;
        box-shadow: none;
        border: none;
        border-radius: 0px;
        padding: 0px;
    }
    .recipe_box:hover{
        box-shadow: none;
        -webkit-transform: scale(1,1);
                transform: scale(1,1);
    }
    .recipe_image{
        width: 100vw;
        border-radius: 0px;
    }
    .recipe_ingrediants{
        padding: 1rem;
    }
    .recipe_view_button{
        border-radius: 0px;
    }
    .recipe_box h3{
        padding: 1rem;
    }
}
